import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index'
import IndexM from '@/views/index_m'
import Login from '@/components/Login'
import User from '@/components/Personal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/m',
      name: 'IndexM',
      component: IndexM
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/user',
      name: 'User',
      component: User
    }
  ]
})



