<template lang="html">
  <div class="hospital-departments">
  <section class="hospital-department">
    <div class="department-container">
       <ul class="department-list" >
         <li class="department-item" v-for="(item, index) in projects"  @click="slide_toggle(index)" :class="{active: i==index }">
           <i class="item-icon"></i>
           <p class="item-title">{{item}}</p>
         </li>
       </ul>
    </div>
  </section>
  <doctors :dp="display"></doctors>
  </div>
</template>

<script>
import Doctors from './Doctors'

export default {
  name: 'HospitalDepartments',
  data(){
    return {
      display: 'none',
      i: '-1',
      projects: ['骨科', '儿科', '妇科', '口腔科', '男科', '心血管内科', '消化内科', '皮肤科', '神经内科', '耳鼻咽喉科' ]
    }
  },
  methods: {
    slide_toggle: function($index){
      this.i = $index
      var items = $(this.$el).find('.department-item')[this.i]
      var pp = (this.projects)[this.i]
      console.log(pp);
      var doctors = $(this.$el).find('.doctors')
      doctors.slideDown('500')
    }
  },
  components: {
    Doctors
  }
}
</script>

<style lang="css" scoped>
  .hospital-departments{
    width: 100%;
    display: none;
    overflow: hidden;
  }
  .department-container{
    width: 100%;
    margin: 0 auto;
    border-radius: 6px;
  }
  .department-list{
    width: 100%;
    white-space: nowrap;
    overflow-x: auto;
    padding-bottom: 15px;
    background: linear-gradient(180deg, #7ab9e4, #67a8e4 80%, rgba(242, 248, 253, 0.1));
  }
  .department-item{
    width: 80px;
    height: 80px;
    display: inline-block;
    /*border: 1px solid rgba(255,255,255,0.5);*/
    border-radius: 15px;
    margin: 5px;
    margin-left: 0;
    transition: all 0.3s linear;
  }
  .active{
      background-color: #0e1435;
  }
  .department-item .item-icon{
    display: block;
    margin: 10px auto;
    width: 45%;
    padding-bottom: 45%;
    /*background-size: 100% 100%;*/
  }
  .department-item .item-title{
    text-align: center;
    margin-top: 5px;
    color: #fff;
  }
  .department-list>.department-item:nth-of-type(1) .item-icon{
    background: url(../assets/group_imgs/bone.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(2) .item-icon{
    background: url(../assets/group_imgs/child.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(3) .item-icon{
    background: url(../assets/group_imgs/female.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(4) .item-icon{
    background: url(../assets/group_imgs/mouth.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(5) .item-icon{
    background: url(../assets/group_imgs/male.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(6) .item-icon{
    background: url(../assets/group_imgs/xxg.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(7) .item-icon{
    background: url(../assets/group_imgs/xh.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(8) .item-icon{
    background: url(../assets/group_imgs/pf.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(9) .item-icon{
    background: url(../assets/group_imgs/sj.png) no-repeat center center;
    background-size: cover;
  }
  .department-list>.department-item:nth-of-type(10) .item-icon{
    background: url(../assets/group_imgs/ebh.png) no-repeat center center;
    background-size: cover;
  }
</style>



