<template lang="html">
  <div class="doctors" :style="{display: dp}">
    <title-component :title= "title" :dp="display"></title-component>
    <section class="doctor">
      <ul class="doctor-list">
        <template v-for="(person, index) in people">
          <li class="doctor-item"  @click= "slide_toggle(index)">
            <div class="item-img" :style="{backgroundImage: people[index].img}"></div>
            <p class="item-title">{{person.title}}</p>
            <span class="item-location">{{person.location}}</span><span class="item-group">{{person.group}}</span>
            <p class="item-act">擅长：{{person.act}}</p>
          </li>
          <house-call></house-call>
        </template>
      </ul>
      <p class="loadmore">加载更多医生</p>
    </section>
  </div>
</template>

<script>
import TitleComponent from './Title'
import HouseCall from './HouseCall'

export default {
  name: 'doctors',
  methods: {
    slide_toggle: function(index) {
      this.i = index
      var li = $(this.$el).find('.doctor-item')[this.i]
      $(li).find('.item-act').toggleClass('active')
      $(li).next('.house-call').slideToggle('300')
    }
  },
  data() {
    return {
      i: '-1',
      title: '热门医生',
      display: 'none',
      people: [
        {title: '阿南达',location:'国际蒙医医院', group: '肝胆科', act: "蒙医蒙药治疗肝、胆、肾病，慢性病、老年病，在继承家族袓传秘方的基础上，研制成专药《肭吉咛布》，用于治疗肝癌病人。2010年授予\“全国医药卫生系统先逬个人称号\”2011年授予内案古自治区首批\"基层名蒙医称号\”2012年荣获第50届世界传统医学\“传统医学金手指\“传统 医学终", img: 'url(static/imgs/and.jpg)'},
        {title: '郑瑜',location:'内蒙古医学院', group: '内科', act: '内科外科肝胆科结合治疗各类疾病结合治疗各类疾病', img: 'url(static/imgs/zy.jpg)'},
        {title: '郭辉',location:'内蒙古医学院', group: '神经外科', act: '顽固性癫痫手术治疗', img: 'url(static/imgs/gh.jpg)'}
      ]
    }
  },
  components: {
    TitleComponent,
    HouseCall
  },
  props: ['dp']
}
</script>

<style lang="css" scoped>
  .doctors{
    /*display: none;
    overflow: hidden;*/
  }
  .active{
    white-space: normal!important;
    height: auto;
    /*padding-bottom: 10px;
    border-bottom: 1px solid #9c9c9c;*/
  }
  .doctor{
    width: 100%;
    background: #fff;
  }
  .doctor-list{
    margin: 9px 0 5px 0px;
    width: 100%;
  }
  .doctor-list li{
    display: block;
    background-color: #FFF;
    width: 100%;
    min-height: 75px;
    border-bottom: 2px solid rgba(79, 187, 83, 0.8) ;
    position: relative;
    cursor: pointer;
  }
  .doctor-item{
    color: #9c9c9c;
    padding: 9px 0 5px 10px;
  }
  .doctor-item>.item-img{
    border-radius: 50%;
    width: 46px;
    height: 46px;
    float: left;
  }
  .item-img{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-size: cover;
  }
  .doctor-item>.item-title{
    margin-left: 60px;
    font-size: 1.1em;
    line-height: 20px;
    color: #333;
  }
  .doctor-item>.item-location {
    margin-left: 14px;
    margin-top: 7px;
    line-height: 20px;
    display: inline-block;
  }
  .doctor-item>.item-group{
    position: relative;
    left: 20px;
  }
  .doctor-item>.item-group::before{
    content: '|';
    position: absolute;
    left: -10px;
    top: -1px;
  }
  .doctor-item>.item-act{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding-left: 10px;
    padding-right: 10px;
    line-height: 20px;
    margin-top: 5px;
  }
  .loadmore{
    margin-top: 10px;
    padding: 6px 0;
    text-align: center;
    color: #0070c9;
  }
</style>



