<template lang="html">
  <div class="login">
    <div class="title">
      <p>快速登陆</p>
    </div>
      <div class="cell">
        <div class="phone label">
          <label for="phone"><img src="static/imgs/phone.svg" alt=""></label>
          <input type="phone" name="" value="" maxlength="11" placeholder="输入你的手机号">
        </div>
        <div class="code label">
          <label for="code"><img src="static/imgs/code.svg" alt=""></label>
          <input type="phone" name="" value="" maxlength="4" placeholder="输入你的验证码" >
          <span class="get-code">获取验证码</span>
        </div>
      </div>
    <div class="submit">
      <input type="submit" name="" value="确定">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login'
}
</script>

<style lang="css" scoped>
::-webkit-input-placeholder { /* WebKit browsers */
color: #999;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
color: #999;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
color: #999;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
color: #999;
}
.login{
  width: 100%;
}
.title{
  color: #3c3c3c;
}
.cell{
  width: 100%;
  padding: 5px 10px;
}
.label{
  width: 100%;
  display: flex;
}
label{
  width: 40px;
  vertical-align: middle;
  display: flex;
  align-items: center;
  text-align: center;
  border: 0;
}
label img{
  width: 24px;
  height: 24px;
  vertical-align: middle;
  display: inline-block;
}
input{
  flex: 1;
  padding: 8px;
  border: 0;
  outline: 0;
}
.label{
  border-bottom: 1px solid #eee;
}
.get-code{
  padding: 8px 5px;
  line-height: 24px;
  color: #2196f3;
  border: 1px solid rgba(213, 213, 213, 0.66);
}
.submit{
  width: 100%;
}
input[type="submit"]{
  width: 90%;
  padding: 12px;
  display: block;
  margin: 10px auto;
  background-color: #2196F3;
  color: #fff;
}
</style>



