<template lang="html">
  <section class="project-detail">
  <div class="detail-container">
    <div class="container-box">
      <span class="container-box-title">
        <span class="line"></span>本套餐共有 {{num}} 项体检项目
      </span>
      <span class="container-box-fee"><span class="line"></span>医道独享价： <span class="fee">{{fee}}元</span></span>
      <p class="container-box-define"><span class="line"></span>本套餐主要针对心脑血管病、颈椎病、肝病、肾病等慢病及常见肿瘤的健康筛查。
      </p>
    </div>
    <div class="container-list">
      <ol class="project-detail-list">
        <template v-for="(project, index) in projects">
          <li class="detail-title" @click="slide_toggle(index)"><span>{{index + 1}}</span>{{project.title}}<span class="toggle"></span></li>
            <p class="details">{{project.details}}</p>
        </template>
      </ol>
    </div>
    <div class="project-pay">
       <span class="project-pay-collect">收藏此套餐</span>
       <span class="project-pay-buy">订购此套餐</span>
    </div>
  </div>
</section>
</template>

<script>
export default {
  name: 'ProjectDetail',
  computed: {
    num: function(){
      return this.projects.length
    }
  },
  methods: {
    slide_toggle: function(index){
      this.i = index
      var project = $(this.$el).find('.detail-title')[this.i]
      var details = $(project).next('.details')
      var span = $(project).find('span')
      $(span).toggleClass('span-act')
      details.slideToggle('300')
    }
  },
  data(){
    return {
      i : 'null',
      fee: '369',
      projects: [
        {title: '耳鼻咽喉科', details: '通过对耳、鼻、咽等器官的常规器械检查，发现常见疾病。'},
        {title: '内科检查', details: '通过对营养,面容,体态,心脏,肺部,腹部,肝脏,脾脏。 通过视、触、叩、听检查心、肺、肝、脾等重要脏器的基本状况，发现常见疾病的相关征兆，或初步排除常见疾病。'},
        {title: '外科检查', details: '检查皮肤、浅表淋巴结、甲状腺、脊柱、四肢关节、肛门、直肠、前列腺、外生殖器等有无异常'},
        {title: '眼科检查', details: '检查眼部疾病'},
        {title: '尿常规', details: '了解泌尿系统代谢功能、筛查肾炎、尿路感染、尿血、糖尿病等'},
        {title: '肾功三项', details: '检测肾功能，协助诊断肾脏疾病、尿毒症、以及痛风等。'},
        {title: '空腹血糖(FBG)', details: '评价人体空腹状态下糖代谢是否正常，评估糖尿病患者空腹血糖控制是否达标。空腹血糖是诊断糖代谢紊乱的最常用和最重要指标。'},
        {title: '肝功八项', details: '急性胆囊炎、胆道堵塞、黄疸症、胆结石、肝硬化、肝脓肿、肝癌、脂肪肝、肝萎缩、急慢性肝炎、中毒性肝炎、酒精性肝炎、低蛋白血症、高蛋白血症及肝功能异常等疾病'},
        {title: '血常规', details: '血细胞情况、排除炎症性疾病、血癌、贫血等'}
      ]
    }
  }
}
</script>

<style lang="css">
  .project-detail{
    width: 100%;
    display: none;
    overflow: hidden;
  }
  .detail-container{
    padding-bottom: 15px;
  }

  .detail-container .container-box{
    padding: 15px 0 10px;
    background-color: white;
    font-size: 1.2em;
    padding-left: 10px;
  }
  .detail-container .container-box-fee{
    float: right;
    margin-right: 5px;
  }
  .detail-container .container-box-define{
    margin: 10px 0;
  }
  .line{
    width: 3px;
    height: 12px;
    display: inline-block;
    vertical-align: middle;
    background: rgba(79, 187, 83, 0.8);
    margin-right: 5px;
  }
  .container-list{
    color: white;
  }
  .project-detail-list{
    background: -webkit-linear-gradient(#618ec2, #2e5098);
    background: -o-linear-gradient(#618ec2, #2e5098);
    background: linear-gradient(#618ec2, #2e5098);
    padding-bottom: 5px;
  }
  .detail-title{
    padding: 10px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 1.2em;
    box-shadow: inset 1px 1px 0 rgba(255,255,255,0.1), 2px 2px 6px rgba(0,0,0,0.4);
    cursor: pointer;
  }
  /*.project-detail-list:last-child .details{
    padding-bottom: 5px;
  }*/
  .detail-title>span{
    font-size: 1.5em;
    margin-right: 5px;
    vertical-align: baseline;
  }
  .detail-title>span.toggle{
    float: right;
    display: inline-block;
    width: 26px;
    height: 26px;
    margin-right: 15px;
    background: url(../assets/imgs/target.svg) no-repeat center center;
    background-size: cover;
    transition: all 0.2s ease-out;
  }
  .span-act{
    transform: rotateZ(109deg);
  }
  p.details{
    margin: 5px;
    padding-left: 17px;
    color: #d6d6d6;
    display: none;
    overflow: hidden;
  }

  .project-pay{
    padding: 15px;
    margin-top: -4px;
    background: -webkit-linear-gradient(#618ec2, #2e5098);
    background: -o-linear-gradient(#618ec2, #2e5098);
    background: linear-gradient(#618ec2, #2e5098);
    box-shadow: inset 1px 1px 0 rgba(255,255,255,0.1), 2px 2px 6px rgba(0,0,0,0.4);
  }
  .project-pay span{
    display: inline-block;
    width: 45%;
    cursor: pointer;
    text-align: center;
    font-size: 15px;
    padding: 14px 18px;
    border-radius: 5px;
    text-shadow: 0  -1px 0 rgba(0, 0, 0, 0.1);
    color: rgba(255, 255, 255, 0.75);
    background: #00bff3;
    background: -webkit-linear-gradient(top, #4ec6f3, #1986cc);
    box-shadow: inset 1px 1px 0 rgba(255  , 255, 255, 0.1), 2px 2px 6px rgba(0,0,0,0.4);
  }
  .project-pay span.project-pay-buy{
    float: right;
  }

</style>



