<template lang="html">
  <div>
    <section class="hospital">
      <ul class="hospital-list" :class="{blue: active}">
        <template v-for="(hospital, index) in hospitals">
          <li class="hospital-item" :class="{dark: active}" @click="slide_toggle(index)">
            <div class="item-img" :style="{backgroundImage: hospitals[index].img}"></div>
            <p class="item-title">{{hospital.title}}<span class="flag">{{hospital.flag}}</span></p>
            <p class="item-phone">{{hospital.phone}}</p>
            <p class="item-location">{{hospital.location}}</p>
          </li>
          <hospital-departments></hospital-departments>
        </template>
      </ul>
    </section>
  </div>
</template>

<script>
import TitleComponent from './Title'
import HospitalDepartments from './HospitalDepartments'

export default {
  name: 'hospitals',
  data() {
    return {
      i: '-1',
      title: '热门医院',
      act: false,
      hospitals: [
        {title:'内蒙古自治区肿瘤医院',flag: '公立医院', phone: '0471-2243257', location: '内蒙古呼和浩特市昭乌达路 42 号', img: 'url(static/imgs/nmgzl.jpg)'},
        {title:'内蒙古医科大学附属医院', flag: '公立医院', phone: '0471-3451191', location: '呼和浩特市新华大街 1 号', img: 'url(static/imgs/nmgfy.jpg)'},
        {title:'呼和浩特人民医院', flag: '公立医院', phone: '0471-6620000', location: '呼和浩特市昭乌达路 20 号', img: 'url(static/imgs/nmgmy.jpg)'}
      ]
    }
  },
  methods: {
    slide_toggle: function(index){
      this.i = index
      var li = $(this.$el).find('.hospital-item')[this.i]
      console.log(li);
      $(li).next('div').slideToggle('500')
    }
  },
  components: {
    TitleComponent,
    HospitalDepartments
  },
  props: ['active']
}
</script>

<style lang="css" scoped>
  .hospital{
    width: 100%;
    background-color: #fff;
    padding-bottom: 40px;
  }
  .blue{
    background: linear-gradient(180deg, #fff, #7ab9e4, #67a8e4 60%, rgba(242, 248, 253, 0.5));
  }
  .dark{
    color: #2b2b2b !important;
    border-bottom: 1px solid rgba(255,255,255,0.52) !important;
  }
  .hospital-list{
    margin: 9px 0 5px 0px;
    width: 100%;
  }
  .hospital-list li{
    display: block;
    width: 100%;
    min-height: 70px;
    border-bottom: 2px solid rgba(33, 150, 243, 0.79);
    position: relative;
    color: #9c9c9c;
    padding: 9px 0 5px 10px;
    cursor: pointer;
  }
  .hospital-item>.item-img{
    border-radius: 5px;
    width: 46px;
    height: 46px;
    float: left;
  }
  .item-img{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  .hospital-item p{
    margin-left: 60px;
  }
  .hospital-item .item-title{
    color: #333;
  }
  .hospital-item .flag{
    border: 1px solid #dedede;
    padding: 1px 4px;
    border-radius: 10px;
    margin-left: 10px;
    box-shadow: 2px 1px 4px 1px #87c5da;
  }
  .hospital-item .item-phone{
    margin-top: 4px;
  }
</style>



