<template lang="html">
  <div class="health-container">
    <ul class="health-list">
        <template v-for="(item, index) in healths">
          <li class="health-item" @click="slide_toggle(index)">
            <div class="item-img" :style="{backgroundImage: healths[index].img}"></div>
            <p class="item-title">{{item.title}}<span class="flag">{{item.flag}}</span></p>
            <p class="item-phone">{{item.phone}}</p>
            <p class="item-location">{{item.location}}</p>
          </li>
          <health-projects></health-projects>
        </template>
    </ul>
  </div>
</template>

<script>
import HealthProjects from './HealthProjects'
export default {
  name: 'Healths',
  data(){
    return {
      i: '-1',
      healths: [
        {title: '内蒙古肿瘤医院体检中心', flag: '公立医院', phone: '0471-2243257', location: '呼和浩特市昭乌达路 42 号', img: 'url(static/imgs/nmgzl.jpg)'},
        {title: '内蒙古医科大学附属医院体检中心', flag: '公立医院', phone: '0471-3451678', location: '呼和浩特市新华大街1号附属医院 8 号楼', img: 'url(static/imgs/nmgfy.jpg)'},
        {title: '呼和浩特泽康体检中心', flag: '公立医院', phone: '0471-6511222', location: '呼和浩特市新城区爱民街 35 号', img: 'url(static/imgs/zktj.jpg)'},
        {title: '内蒙古国际蒙医医院体检中心',  flag: '公立医院',phone: '0471-5182020', location: '呼和浩特市大学东街 83 号', img: 'url(static/imgs/nmgmy.jpg)'},
        {title: '内蒙古职工体检中心',  flag: '公立医院',phone: '0471-6292577', location: '呼和浩特市文化宫街 28 号', img: 'url(static/imgs/zgtj.jpg)'}
      ]
    }
  },
  methods: {
    slide_toggle: function(index){
      this.i = index
      var li = $(this.$el).find('.health-item')[this.i]
      var health_projects = $(li).next('.health-projects')
      health_projects.slideToggle('500')
    }
  },
  components: {
    HealthProjects
  }
}
</script>

<style lang="css" scoped>
  .health-container{

  }
  .health-list{
    background: linear-gradient(180deg, #fff, #7ab9e4, #67a8e4 60%, rgba(242, 248, 253, 0.5));
    padding-bottom: 50px;
    margin: 0;
  }
  .health-item{
    display: block;
    width: 100%;
    min-height: 70px;
    border-bottom: 1px solid rgba(255,255,255,0.52);
    position: relative;
    color: #2b2b2b;
    padding: 9px 0 5px 10px;
  }
  .health-item>.item-img{
    border-radius: 5px;
    width: 46px;
    height: 46px;
    float: left;
  }
  .item-img{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .health-item p{
    margin-left: 60px;
  }
  .health-item .item-title{
    color: #333;
  }
  .item-title .flag{
    border: 1px solid #dedede;
    padding: 1px 4px;
    border-radius: 10px;
    margin-left: 10px;
    box-shadow: 2px 1px 4px 1px #87c5da;
  }
  .health-item .item-phone{
    margin-top: 4px;
    line-height: 1.5em;
  }
  .health-item .item-location{
    line-height: 1.5em;
  }
</style>



